import React from 'react'
import { Link } from 'react-router-dom';

import rock from './img/rock.png';
import paper from './img/paper.png';
import scissors from './img/scissors.png';

const Symbols = ['Rock', 'Paper', 'Scissors']; 
const Results = ['Draw', 'Win', 'Lose']; 

function GameResult (props) {


  return (
            <div>
                <div> 
                    Game finished: <br></br>
                    Player: {Symbols[props.userSymbol]} <br></br>
                    Machine: {Symbols[props.contractSymbol]} <br></br>

                    <b>RESULT: {Results[props.gameResult]}</b>         
                </div>
                <div className = "ergebnis"> 
                    {(props.userSymbol == 0 && props.gameResult != 1) && <div> <img alt="Rock" src={rock} /><br/> Player took: {Symbols[props.userSymbol]}</div>}
                    {(props.userSymbol == 1 && props.gameResult != 1) && <div> <img alt="Paper" src={paper} /><br/> Player took: {Symbols[props.userSymbol]}</div>}
                    {(props.userSymbol == 2 && props.gameResult != 1) && <div> <img alt="Scissors" src={scissors} /><br/> Player took: {Symbols[props.userSymbol]}</div>}  
                    {(props.userSymbol == 0 && props.gameResult == 1) && <div> <img alt="Rock" className = "blink" src={rock} /><br/> Player took: {Symbols[props.userSymbol]}</div>}
                    {(props.userSymbol == 1 && props.gameResult == 1) && <div> <img alt="Paper" className = "blink" src={paper} /><br/> Player took: {Symbols[props.userSymbol]}</div>}
                    {(props.userSymbol == 2 && props.gameResult == 1) && <div> <img alt="Scissors" className = "blink" src={scissors} /><br/> Player took: {Symbols[props.userSymbol]}</div>}  
                
                </div>
                <div> 
                    {(props.contractSymbol == 0 && props.gameResult != 2) && <div> <img alt="Rock" src={rock} /><br/> Oracle took: {Symbols[props.contractSymbol]}</div>}
                    {(props.contractSymbol == 1 && props.gameResult != 2) && <div> <img alt="Paper" src={paper} /><br/> Oracle took: {Symbols[props.contractSymbol]}</div>}
                    {(props.contractSymbol == 2 && props.gameResult != 2) && <div> <img alt="Scissors" src={scissors} /><br/>Oracle took: {Symbols[props.contractSymbol]}</div>}  
                    {(props.contractSymbol == 0 && props.gameResult == 2) && <div> <img alt="Rock" className = "blink" src={rock} /><br/> Oracle took: {Symbols[props.contractSymbol]}</div>}
                    {(props.contractSymbol == 1 && props.gameResult == 2) && <div> <img alt="Paper" className = "blink" src={paper} /><br/> Oracle took: {Symbols[props.contractSymbol]}</div>}
                    {(props.contractSymbol == 2 && props.gameResult == 2) && <div> <img alt="Scissors" className = "blink" src={scissors} /><br/>Oracle took: {Symbols[props.contractSymbol]}</div>}  
                </div>
            
                <br></br>
                <Link to="/">
                    <button type="button" className='cta-button mint-nft-button'>
                        Play it again ...
                    </button>
                </Link>
            </div>
        );
}

export default GameResult;