import { ethers } from 'ethers';
import { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom'

//import contract from './contracts/RockPaperScissors.json';
import contract from './contracts/RockPaperScissors_VRF.json';

import LoseWin from './LoseWin';
import LastSymbols from './LastSymbols';

export default function Navbar() {


  const location = useLocation();
 

  let url = process.env.REACT_APP_CHAIN_URL;
  let provider = new ethers.providers.JsonRpcProvider(url);

  const contractAddress = process.env.REACT_APP_CONTRACT_ADDRESS;
  const abi = contract.abi;

  const [contractBalance, setContractBalance] = useState(null);
  const [currentAccount, setCurrentAccount] = useState(null);
  const [eventData, setEventData] = useState(null);

  const connectWallet = async () => {
    const { ethereum } = window;
    const accounts = await ethereum.request({method: 'eth_requestAccounts' });
    if(accounts.length !==0){
        const account = accounts[0];
        console.log("Found an account! ", account);
        setCurrentAccount(account);
        return account;
    }else{
        console.log("No authorized Account found !");
    }
  }

  const getContractBalance = async () => { 
    const balance = await provider.getBalance(contractAddress);
    setContractBalance(ethers.utils.formatEther(balance));
    console.log(`\nETH Balance of ${contractAddress} --> ${ethers.utils.formatEther(balance)} ETH\n`);
    return balance;
  }

  const getContractEvents = async () => { 
    const block = await provider.getBlockNumber()
    console.log("Blocknumber : " + block)

    const contract = new ethers.Contract(contractAddress, abi, provider)
    const transferEvents = await contract.queryFilter('Game', block - 1000, block)
    /*
    transferEvents.map((events) => (
      console.log("\nHistorical Games: " + events.args)
    ))
    */
    await setEventData(transferEvents);
    //console.log("eventData_getEvents " + await setEventData(transferEvents));

    return transferEvents;
  }


  useEffect(() => {
    getContractBalance();
    getContractEvents();
    connectWallet();
  }, [location])


  return (
    <div>
     { eventData && <div className="navbar"><div>Balance = <b>{contractBalance}</b></div> <LoseWin eventData={eventData}/><div>Willkommen = <b>{currentAccount.substr(0,5)+'...'}</b></div></div> }
     { eventData && <div className="navbar"><LastSymbols eventData={eventData}/></div> }
      <h1>Have Fun with Rock Paper Scissors !</h1>
    </div>
  )
}



