import React from 'react'

import rock from './img/rock.png';
import paper from './img/paper.png';
import scissors from './img/scissors.png';

const Symbols = ['Rock', 'Paper', 'Sissors']; 

function GameStart (props) {

    //console.log("props GameStart: " + props)

  return (
    <div> 
    {(props.userSymbol == 0) && <div> <img alt="Rock" src={rock} /><br/> Make your deposit for: {Symbols[props.userSymbol]}</div>}
    {(props.userSymbol == 1) && <div> <img alt="Paper" src={paper} /><br/> Make your deposit for: {Symbols[props.userSymbol]}</div>}
    {(props.userSymbol == 2) && <div> <img alt="Scissors" src={scissors} /><br/> Make your deposit for: {Symbols[props.userSymbol]}</div>}  
  </div>
  );
}

export default GameStart;