import React from 'react';
import { useParams } from 'react-router-dom';
//import contract from './contracts/RockPaperScissors.json';
import contract from './contracts/RockPaperScissors_VRF.json';

import { ethers } from 'ethers';
import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import GameStart from './GameStart';
import GamePending from './GamePending';
import GameResult from './GameResult';


export const Game = () => {

    const{id} = useParams();
    //Local Chain Ganache
    const contractAddress = process.env.REACT_APP_CONTRACT_ADDRESS;
    const abi = contract.abi;
    const symbol = id; 
    
    const [userSymbol, setUserSymbol] = useState(null);
    const [contractSymbol, setContractSymbol] = useState(null);
    const [gameResult, setGameResult] = useState(null);

    const [isPending, setIsPending] = useState(false);
    const [isStarting, setIsStarting] = useState(true);
    const [isCompleted, setIsCompleted] = useState(false);
    const [isError, setIsError] = useState(false);


    useEffect(() => {
        setUserSymbol(symbol);
        playRPSHandler(symbol);
      }, [])

   
    const playRPSHandler = async (symbol) => {   
        try{
            const { ethereum } = window;
            if (ethereum){
                console.log("Start play ...");
                const provider = new ethers.providers.Web3Provider(ethereum);
                const signer = provider.getSigner();
                const rpsContract = new ethers.Contract(contractAddress, abi, signer);
                console.log("Contract balance : " + await rpsContract.getBalance());
               // Initialize payment 
                console.log("Initialize payment");
                let  gasLimit = await rpsContract.estimateGas.play(symbol, {value: ethers.utils.parseEther("0.05")});
                //let gas = (await provider.getFeeData()).maxFeePerGas.mul(gasLimit);
                //let gas = (await provider.getFeeData()).maxFeePerGas*gasLimit;
                let gas = (await provider.getGasPrice())*gasLimit;
                console.log("Gas: " + gas);
                let rpsTxn = await rpsContract.play(symbol, {value: ethers.utils.parseEther("0.05")})
                //let rpsTxn = await rpsContract.play(symbol, {value: ethers.utils.parseEther("0.000000000000000005")})
                //let rpsTxn = await rpsContract.play(0, {value: ethers.utils.formatEther("5")})
                console.log("Play ... " + symbol)
            
                setIsPending(true);
                setIsStarting(false);

                rpsContract.on("Game", (player, players_choice, contracts_choice, result_game, event) => {
                  let info = {
                    player: player,
                    players_choice: parseInt(players_choice),
                    contracts_choice: parseInt(contracts_choice),
                    result_game: result_game,
                    data: event,
                  };
                  //setTimeout(() => {
                    setGameResult(result_game);
                    setUserSymbol(players_choice);
                    setContractSymbol(contracts_choice);   
                    setIsPending(false);
                    setIsCompleted(true);
                  //}, 3000);

                  //console.log(JSON.stringify(info, null, 4));
                  //console.log("JSON fired");
                  //console.log("isPending " + isPending);

                });
                


                
                await rpsTxn.wait();
                
                console.log("Played ..., see Polygon Testnet " + rpsTxn.hash);
    

            } else {
                console.log("Etheruem Object does not exist");
            }
        }catch (error){
            console.log(error);
            setIsError(true);
        }
      }


    
    return (
        <div>
            {(isStarting && !isError) && <GameStart userSymbol={userSymbol}/>}
            {(isPending && !isError) && <GamePending userSymbol={userSymbol}/>}
            {(isCompleted && !isError) && <GameResult userSymbol={userSymbol} contractSymbol={contractSymbol} gameResult={gameResult}/>}
            {(isError) && <div><h1>An error occurred try again</h1>
            <br></br>
            <Link to="/">
                <button type="button" className='cta-button mint-nft-button'>
                    Play it again ...
                </button>
            </Link>
            </div>
            }
        </div>
  )
}
